const BASE_URL = '/api/v1';

// Action Names
export const FETCH_SITES = 'FETCH_SITES'

// SitesPage
export function fetchSites() {
  const url = `${BASE_URL}/sites`;
  const promise = fetch(url, { credentials: "same-origin" }).then(r => r.json());

  return {
    type: FETCH_SITES,
    payload: promise
  };
}
