import { combineReducers } from 'redux'
import {FETCH_SITES } from '../actions'


export const initialState = {
  sites: [],
};

export const rootReducer = combineReducers({
  sites: sites,
})

//=======================================================

function sites(state = null, action) {
   switch (action.type) {
     case FETCH_SITES:
       return action.payload;
      default:
        return state;
    }
  }